// Carousels
// @import '../spectre-variables.less';
// @import '../spectre-variables.scss';

#bulma-version {

	.button {
		display: block;
		position: relative;
	}

	.text-hide {
		display: none;
		color: transparent;

		text-indent: -1000px;

		&.hand {
			cursor: pointer;
		}
	}



	.carousel {
		background: #ff0000;
		display: block;
		overflow: hidden;
		position: relative;
		width: 100%;

		.carousel-container {
			height: 100%;
			left: 0;
			position: relative;
			&::before {
				content: "";
				display: block;
				padding-bottom: 56.25%;
			}

			.carousel-item {
				animation: carousel-slideout 1s ease-in-out 1;
				height: 100%;
				left: 0;
				margin: 0;
				opacity: 0;
				position: absolute;
				top: 0;
				width: 100%;

				&:hover {
					.item-prev,
					.item-next {
						opacity: 1;
					}
				}
			}

			.item-prev,
			.item-next {
				// color: darken($orange, 5%);

				// background: fade($grey-light, 25%);
				// border-color: fade($grey-light, 50%);
				background: transparentize($grey-light, 0.25);
				border-color: transparentize($grey-light, 0.50);
				color: $grey;
				opacity: 0;
				position: absolute;
				top: 50%;
				transition: all .4s ease;
				transform: translateY(-50%);
				z-index: $zindex-2;
			}
			.item-prev {
				left: 2rem;
			}
			.item-next {
				right: 2rem;
			}
		}

		.carousel-locator {
			&:nth-of-type(1):checked ~ .carousel-container .carousel-item:nth-of-type(1),
			&:nth-of-type(2):checked ~ .carousel-container .carousel-item:nth-of-type(2),
			&:nth-of-type(3):checked ~ .carousel-container .carousel-item:nth-of-type(3),
			&:nth-of-type(4):checked ~ .carousel-container .carousel-item:nth-of-type(4) {
				animation: carousel-slidein .75s ease-in-out 1;
				opacity: 1;
				z-index: $zindex-1;
			}
			&:nth-of-type(1):checked ~ .carousel-nav .nav-item:nth-of-type(1),
			&:nth-of-type(2):checked ~ .carousel-nav .nav-item:nth-of-type(2),
			&:nth-of-type(3):checked ~ .carousel-nav .nav-item:nth-of-type(3),
			&:nth-of-type(4):checked ~ .carousel-nav .nav-item:nth-of-type(4) {
				color: $grey-light;
			}
		}

		.carousel-nav {
			bottom: 1rem;
			display: flex;
			justify-content: center;
			left: 50%;
			position: absolute;
			transform: translateX(-50%);
			width: 20rem;
			z-index: $zindex-2;

			.nav-item {
				color: fade($grey-light, 50%);
				display: block;
				flex: 1 0 auto;
				height: 4rem;
				margin: .4rem;
				max-width: 5rem;
				position: relative;

				&::before {
					background: currentColor;
					content: "";
					display: block;
					height: .3rem;
					position: absolute;
					top: 2rem;
					width: 100%;
				}
			}
		}
	}

	@keyframes carousel-slidein {
		0% {
			transform: translateX(100%);
		}
		100% {
			transform: translateX(0);
		}
	}

	@keyframes carousel-slideout {
		0% {
			opacity: 1;
			transform: translateX(0);
		}
		100% {
			opacity: 1;
			transform: translateX(-50%);
		}
	}




}