// Override Bulma variables here.
// This is used both by Bulma's sass source and
// the site scss.

////////////////////////////////////////////////
////////////////////////////////////////////////
// 1. Initial variables
// http://paletton.com/#uid=1000u0kllllaFw0g0qFqFg0w0aF

// Colors
$black: hsl(0, 0%, 4%) !default;
$black-bis: hsl(0, 0%, 7%) !default;
$black-ter: hsl(0, 0%, 14%) !default;

$grey-darker: hsl(0, 0%, 21%) !default;
$grey-dark: hsl(0, 0%, 29%) !default;
$grey: hsl(0, 0%, 48%) !default;
$grey-light: hsl(0, 0%, 71%) !default;
$grey-lighter: hsl(0, 0%, 86%) !default;

$white-ter: hsl(0, 0%, 96%) !default;
$white-bis: hsl(0, 0%, 98%) !default;
$white: hsl(0, 0%, 100%) !default;

$orange: hsl(42, 100%, 44%) !default;
$yellow: hsl(48, 100%, 67%) !default;

// $green:     hsl(141, 71%,  48%) !default
$green: #007F49 !default;
$turquoise: hsl(171, 100%, 41%) !default;

// $blue:      hsl(197, 71%,  73%) !default
$blue: #260A80 !default;
$purple: hsl(271, 100%, 71%) !default;
$red: #cc3333 !default;

// Typography
// $family-sans-serif: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", "Helvetica", "Arial", sans-serif !default
$family-sans-serif: "Helvetica Neue", "Helvetica", "Arial", sans-serif !default;
$family-monospace: monospace !default;
$family-serif-title: "Portrait Web", Georgia;

$size-1: 3rem !default;
$size-2: 2.5rem !default;
$size-3: 2rem !default;
$size-4: 1.5rem !default;
$size-5: 1.25rem !default;
$size-6: 1rem !default;
$size-7: 0.75rem !default;

$weight-light: 300 !default;
$weight-normal: 400 !default;
$weight-semibold: 500 !default;
$weight-bold: 700 !default;

// Body
$body-background: $white !default;
$body-size: 16px !default;

// Responsiveness
// 960, 1152, and 1344 have been chosen because
// they are divisible by both 12 and 16
$tablet: 769px !default;

// 960px container + 40px
$desktop: 1000px !default;

// 1152px container + 40
$widescreen: 1192px !default;

// 1344px container + 40
$fullhd: 1384px !default;

// Miscellaneous
$easing: ease-out !default;
$radius-small: 2px !default;
$radius: 3px !default;
$radius-large: 5px !default;
$speed: 86ms !default;

////////////////////////////////////////////////
////////////////////////////////////////////////
// 2. Primary colors

$primary: $black !default;

$info: $orange !default;
$success: $green !default;
$warning: $yellow !default;
$danger: $red !default;

$light: $white-ter !default;
$dark: $grey-darker !default;

////////////////////////////////////////////////
////////////////////////////////////////////////
// 3. Applied variables

// Invert colors
$black-invert: $white;
$orange-invert: findColorInvert($orange) !default;
$yellow-invert: findColorInvert($yellow) !default;
$green-invert: findColorInvert($green) !default;
$turquoise-invert: findColorInvert($turquoise) !default;
$blue-invert: findColorInvert($blue) !default;
$purple-invert: findColorInvert($purple) !default;
$red-invert: findColorInvert($red) !default;

$primary-invert: $black-invert !default;
$info-invert: $black !default;
$success-invert: $green-invert !default;
$warning-invert: $yellow-invert !default;
$danger-invert: $red-invert !default;
$light-invert: $dark !default;
$dark-invert: $light !default;

// General colors
$background: $white-ter !default;

$border: $grey-lighter !default;
$border-hover: $grey-light !default;

// Text colors
$text: $grey-dark !default;
$text-invert: findColorInvert($text) !default;
$text-light: $grey !default;
$text-strong: $grey-darker !default;

// Code colors
$code: $blue !default;
$code-background: $background !default;

$pre: $text !default;
$pre-background: $background !default;

// Link colors
$link: $info !default;
$link-invert: $info-invert !default;
$link-visited: darken($info, 15%) !default;

$link-hover: darken($info, 5%) !default;
$link-hover-border: darken($info, 5%) !default;

$link-focus: darken($info, 5%) !default;
$link-focus-border: darken($info, 5%) !default;

$link-active: darken($info, 15%) !default;
$link-active-border: darken($info, 15%) !default;

// Typography
$family-primary: $family-sans-serif !default;
$family-code: $family-monospace !default;

$size-small: $size-7 !default;
$size-normal: $size-6 !default;
$size-medium: $size-5 !default;
$size-large: $size-4 !default;

////////////////////////////////////////////////
////////////////////////////////////////////////
// 4. Lists and maps

$colors: (
    white: ($white, $black), 
    black: ($black, $white), 
    light: ($light, $light-invert), 
    dark: ($dark, $dark-invert), 
    primary: ($primary, $primary-invert), 
    info: ($info, $info-invert), 
    danger: ($danger, $danger-invert), 
    success: ($success, $success-invert), 
    warning: ($warning, $warning-invert), 
    orange: ($orange, $orange-invert), 
    yellow: ($yellow, $yellow-invert), 
    green: ($green, $green-invert), 
    turquoise: ($turquoise, $turquoise-invert), 
    blue: ($blue, $blue-invert), 
    purple: ($purple, $purple-invert), 
    red: ($red, $red-invert)
) !default;

// @warn 'danger-invert:'
// @warn $danger-invert
// @warn darken($danger-invert, 5%)

$sizes: $size-1 $size-2 $size-3 $size-4 $size-5 $size-6 !default;

////////////////////////////////////////////////
////////////////////////////////////////////////
// Shadows

$shadow-offset-x: 4px;
$shadow-offset-y: 4px;
$shadow-spread: 10px;
$shadow-color: $grey-light;
$primary-box-shadow: $shadow-offset-x $shadow-offset-y $shadow-spread $shadow-color;
