////////////////////////////////////////////////
// Miscellaneous settings

.is-grey {
  color: $grey-light;
}

tt {
  color: $grey;
  background-color: $grey-lighter;
  border-radius: 3px;
  padding: 1px 3px;
}

.is-text-red {
  color: $red;
}

.has-padding {
  padding: 1em;
}

.has-padding-more {
  padding: 2.5em;
}


// Tables that use the "chip" user/avatar layout 
// or we just want vertically centered rows
table.has-chips, table.has-rows-vertical-centered {
  td {
    vertical-align: middle;
  }
}
